@value makerGreen, makerWhite from "../../common/colors.css";
.login {
  width: 30rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: makerWhite;
}
.list {
  width: 100%;
  display: flex;
  flex-direction: column;
  list-style: none;
  padding: 0.5rem;
}

.item {
  margin-bottom: 0.5em;
}
.button {
  width: 100%;
  height: 2.5em;
  font-size: 1.2rem;
  outline: 0;
  cursor: pointer;
  border: 0.2rem solid makerGreen;
  border-radius: 1.2rem;
}
