@value makerGreen, makerWhite from '../../common/colors.css';

.header {
  width: 100%;
  text-align: center;
  padding: 1em;
  background-color: makerGreen;
  position: relative;
}

.logo {
  width: 5em;
}

.title {
  margin: 0;
  color: makerWhite;
}

.logout {
  position: absolute;
  right: 3em;
  top: 1em;
  padding: 0.2em;
  border-radius: 10%;
  background-color: lightGreen;
  outlien: 0;
  cursor: pointer;
}
